.App {
    text-align: center;
    font-family: Arial, sans-serif;
}

.App-logo {
    height: 60px;
}

.App-header {
    background-color: #fff;
    height: 70px;
    padding: 40px;
    color: #d0d0d0;
    border-bottom: 1px solid #d0d0d0;
}

.Button-list {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
}

.Button-list .Button-block {
    width: 33%;
    -ms-flex-direction: row;
        flex-direction: row;
}

.Button-list .Button-block button {
    width: 50%;
    padding: 10px 30px 10px 30px;
    margin: 10px auto;
    background-color: #fff;
    color: #ba001c;
    border: 1px solid #d0d0d0;
    text-transform: uppercase;
}

.Button-list .Button-block button:hover {
    cursor: pointer;
}

.Display {
    min-height: 350px;
    height: 350px;
    display: -ms-flexbox;
    display: flex;
}

.Display .Restaurants,
.Display .Weather,
.Display .SpaBlock {
    width: 33%;
    -ms-flex-direction: row;
        flex-direction: row;
}

.Display .Restaurants .Restaurant{
    display: -ms-flexbox;
    display: flex;
}

.Display .Restaurants .Restaurant .RestaurantRow {
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center;
    width: 50%;
    margin: 10px 0 20px 0;
}

.Display .Restaurants .RestaurantRow div {
    padding: 5px;
    font-size: 0.78rem;
    line-height: 0.78rem;
}

.Display .Restaurants .RestaurantRow .title {
    text-transform: uppercase;
}

.Display .Restaurants .RestaurantRow .info {
    color: #969696;
    margin-bottom: 20px;
}

.Display .Restaurants .RestaurantRow .info div {
    text-transform: uppercase;
}

.Display .Restaurants .RestaurantRow .info .link {
    display: inline;
}

.Display .Weathers .Weather {
    display: -ms-flexbox;
    display: flex;
}

.Display .Weathers .Weather .WeatherRow {
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center;
    width: 50%;
    margin: 10px 0 20px 0;
}

.Display .Weather .WeatherRow div {
    padding: 5px;
    font-size: 0.78rem;
    line-height: 0.78rem;
}

.Display .Weather .WeatherRow .title {
    color: #ba001c;
    border-bottom: 1px solid #d0d0d0;
    text-transform: uppercase;
}

.Display .Weather .WeatherRow .info {
    color: #969696;
    margin-bottom: 20px;
    display: -ms-flexbox;
    display: flex;
}

.Display .Weather .WeatherRow .info div {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 33%;
}

.Display .Weather .WeatherRow .info .icon {
    display: inline;
}

.Display .Weather .WeatherRow .info .tempDay,
.Display .Weather .WeatherRow .info .tempNight {
    vertical-align: middle;
    text-align: center;
    line-height: 58px;
    color: #272727;
}

.Display .Weather .WeatherRow .info .tempNight {
    color: #ababab;
}

.Display .SpaBlock .CouponForm .CouponWrap {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.Display .SpaBlock .CouponForm .CouponWrap div {
    margin: 5px;
    text-align: center;
}

.Display .SpaBlock .CouponForm .CouponWrap .title,
.Display .SpaBlock .CouponForm .CouponWrap .success {
    color: #272727;
    text-transform: uppercase;
    width: 50%;
    font-size: 0.8rem;
    margin-left: 25%;
}

.Display .SpaBlock .CouponForm .clientEmail {
    color: #000;
    background-color: #fff;
    text-transform: uppercase;
    border: 1px solid #d0d0d0;
    width: 220px;
    height: 35px;
}

.Display .SpaBlock .CouponForm .reserveButton {
    color: #fff;
    background-color: #ba001c;
    text-transform: uppercase;
    border: none;
    width: 220px;
    height: 35px;
}

.Display .SpaBlock .CouponForm .reserveButton:hover {
    cursor: pointer;
}

.Display .SpaBlock .CouponForm .CouponWrap .success {
    color: #ba001c;
}

.restaurantWebsite {
    text-decoration: none;
    color: #969696;
}

.restaurantWebsite:hover {
    cursor: pointer;
}

.Display .RestaurantRow .image {
    width: 50%;
    margin: 10px 0 10px 0;
}



